@import url('https://fonts.googleapis.com/css?family=Fjalla+One|Merriweather|Merriweather+Sans');

header {
    position: relative;
    margin: 40px 0px 0 0px;
    padding: 20px 0px 0px 0px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
}

#title {
    height:50%;
    width:99%;
    border-bottom: double 5px #aaa;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align:start;
        align-items:flex-start;
}

header img {
    position:absolute;
    top:0;
    right:0;
    border: none;
    border-radius: 50%;
    height:100%;
    border: double 5px #aaa;
}

#description {
    height:50%;
    font: bold 20px Merriweather, serif;
    text-align: left;
    padding: 5px 0 0 0;
    height: 80px;
}

h1 {
    font: normal 76px Fjalla One, sans-serif;
    letter-spacing: -5px;
}

a {
    color: #111;
    text-decoration: none;
}

a.twitter {
    color: #4099FF;
}

a.linkedin {
    color: #0077B5;
}

a.email {
    color: #C92C19;
}


